





















































































































import { Component, Vue, Watch } from "vue-property-decorator";
import { Action, Getter } from "vuex-class";
type Data = {
  id: number;
  name: string;
  image: string;
  imagePicker: string;
};
@Component({})
export default class Score extends Vue {
  @Getter listPost!: any;
  @Action getData!: () => any;

  @Action("addCat")
  addCat(val: any) {
    this.addCat(val);
  }
  @Action("delCat")
  delCat(val: any) {
    this.delCat(val);
  }
  @Action("updateCat")
  updateCat(val: any) {
    this.updateCat(val);
  }

  created() {
    this.getData();

    // this.initialize();
  }
  customImage: any = null;
  customImagePicker: any = null;
  localImage: any = null;
  localFile: any = null;
  search: string = "";
  imagePreviewURL: string = "";
  imagePickerPreviewURL: string = "";
  dialog: boolean = false;
  dialogDelete: boolean = false;
  headers: Array<object> = [
    {
      text: "Tn thc thn",
      align: "start",
      sortable: false,
      value: "name",
    },

    // { text: "Hnh nh", value: "image" },
    // { text: "Bc/  him", value: "rank" },
    // { text: " u tin", value: "prioritized" },

    { text: "Actions", value: "actions", sortable: false },
  ];

  editedIndex: number = -1;
  editedItem: Data = {
    id: 0,
    name: "",
    image: "",
    imagePicker: "",
  };
  defaultItem: Data = {
    id: 0,
    name: "",
    image: "",
    imagePicker: "",
  };
  get formTitle() {
    return this.editedIndex === -1 ? "New Item" : "Edit Item";
  }
  @Watch("dialog")
  wDialog(val: any) {
    val || this.close();
  }
  @Watch("dialogDelete")
  wDialogDelete(val: any) {
    val || this.closeDelete();
  }

  editItem(item: any) {
    this.editedIndex = this.listPost.indexOf(item);
    this.editedItem = Object.assign({}, item);
    this.imagePreviewURL = item.img;
    this.imagePickerPreviewURL = item.image_choose;

    this.dialog = true;
  }

  deleteItem(item: any) {
    this.editedItem = Object.assign({}, item);

    this.dialogDelete = true;
  }

  deleteItemConfirm() {
    this.delCat(this.editedItem.id);
    this.closeDelete();
  }

  close() {
    this.dialog = false;
    this.customImage = null;
    this.imagePreviewURL = "";
    this.customImagePicker = null;
    this.imagePickerPreviewURL = "";
    this.$nextTick(() => {
      this.editedItem = Object.assign({}, this.defaultItem);
      this.editedIndex = -1;
    });
  }
  closeDelete() {
    this.dialogDelete = false;
    this.$nextTick(() => {
      this.editedItem = Object.assign({}, this.defaultItem);
      this.editedIndex = -1;
    });
  }

  save() {
    if (this.editedIndex > -1) {
      // console.log(this.editedItem);
      let imageName;
      let imageNamePicker;
      if (this.customImage == null) {
        imageName = null;
      } else {
        imageName = new File(
          [this.customImage],
          `${Date.now()}.${this.customImage.name}`,
          { type: this.customImage.type }
        );
      }
      if (this.customImagePicker == null) {
        imageNamePicker = null;
      } else {
        imageNamePicker = new File(
          [this.customImagePicker],
          `${Date.now()}.${this.customImagePicker.name}`,
          { type: this.customImagePicker.type }
        );
      }
      const data = Object.assign({}, this.editedItem, {
        image: imageName,
        imagePicker: imageNamePicker,
      });

      // const data1 = Object.assign({}, this.editedItem, {
      //   imagePicker: imageName,
      // });
      // console.log(data);
      // console.log(data.id);
      this.updateCat(data);
      // this.updateCat(data1);
    } else {
      const data = Object.assign({}, this.editedItem, {
        image: new File(
          [this.customImage],
          `${Date.now()}.${this.customImage.name}`,
          { type: this.customImage.type }
        ),
        imagePicker: new File(
          [this.customImagePicker],
          `${Date.now()}.${this.customImagePicker.name}`,
          { type: this.customImagePicker.type }
        ),
      });

      // console.log(data);
      this.addCat(data);
    }
    this.close();
  }

  onFileChange(event: any) {
    const file = event;

    this.customImage = event;
    if (file) {
      this.imagePreviewURL = URL.createObjectURL(file);

      URL.revokeObjectURL(file); // free memory
    } else {
      this.imagePreviewURL = "";
      this.customImage = null;
    }
  }

  onFileChangePicker(event: any) {
    const file = event;

    this.customImagePicker = event;
    if (file) {
      this.imagePickerPreviewURL = URL.createObjectURL(file);

      URL.revokeObjectURL(file); // free memory
    } else {
      this.imagePickerPreviewURL = "";
      this.customImagePicker = null;
    }
  }

  filterOnlyCapsText(value: string, search: string) {
    return (
      value != null &&
      search != null &&
      typeof value === "string" &&
      value.toString().toLocaleLowerCase().indexOf(search) !== -1
    );
  }
}
